<template>
	<el-container class="add-blog">
		<h2>添加博客</h2>
		<el-form ref="blog"  v-if="!submitShow" label-width="80px" size="mini" >
			  <el-form-item label="博客标题" prop="title">
				<el-input v-model="blog.title"></el-input>
			  </el-form-item>
			   <el-form-item label="博客内容" prop="content">
				<el-input type="textarea" v-model="blog.content"></el-input>
			  </el-form-item>
			  <el-form-item prop="type">
				  <el-checkbox  v-model="blog.categories" label="vue.js"></el-checkbox>
				  <el-checkbox label="nodejs" v-model="blog.categories" name="type"></el-checkbox>
				  <el-checkbox label="React.js" v-model="blog.categories" name="type"></el-checkbox>
				  <el-checkbox label="Angular" v-model="blog.categories" name="type"></el-checkbox>
			  </el-form-item>
			  <el-form-item label="活动区域">
				<el-select v-model="blog.auth" placeholder="请选择活动区域">
				  <el-option v-for="author in authors" :key="author" :value="author"></el-option>
				</el-select>
			  </el-form-item>
			  <el-button type="primary" @click="add">添加博客</el-button>
		</el-form>
		<div v-if="submitShow">
			<h2>你的博客添加成功</h2>
		</div>
		<el-card class="box-card">
			<h3>博客总览</h3>
			<p>博客标题{{blog.title}}</p>
			<p>博客内容{{blog.content}}</p>
			<p>博客分类</p>
			<ul>
				<li v-for="category in blog.categories" :key="category">
					{{category}}
				</li>
			</ul>
			<p>作者:{{blog.auth}}</p>
		</el-card>
	</el-container>
	
	
</template>

<script>
	export default{
		name:"add-blog",
		data(){
			return{
				blog:{
					title:"",
					content:"",
					categories:[],
					auth:""
				},
				authors:["溜溜","春春"],
				submitShow:false
			}
		},
		methods:{
			add(e){
				this.$http.post("/api/users/",this.blog)
				.then((data)=>{
					this.$router.push({path:"/",query:{alert:"用户信息添加成功！"}});
					this.submitShow=true
				})
				.catch((error)=>{
					console.log(error)
				})
				e.preventDefault();
			}
	    },
	}
</script>

<style scoped>
	.el-container{flex-direction:column}
	.add-blog{margin: 0 auto;width: 500px;}
	.add-blog h2{width: 100%;}
	.el-form{width: 100%;padding:15px 0;}
</style>



