<template>
	<div id="shopping">
	   <table>
		  <tr>
			  <td><input type="checkbox" v-model='isok' v-on:click='demo'/></td>
			  <td>名称</td>
			  <td>单价</td>
			  <td>数量</td>
			  <td>小计</td>
			  <td>操作</td>
		  </tr>
		  <tr v-for="(item,k) in items">
			  <td><input type="checkbox" :value='item'  v-model='itemsd' @change='demo2'/></td>
			  <td>{{item.name}}</td>
			  <td>${{item.price}}</td>
			  <td>
				  <button @click="subt(k)">-</button>
				  <input type="text" v-model="item.nume"/>
				  <button @click="adds(k)">+</button>
			  </td>
			  
			  <td>${{(item.price)*(item.nume)}}</td>
			  <td><button @click="delect(k)">删除</button></td>
		  </tr>
	   			  
	   </table>
       <p>{{itemsd }}</p>
	   <p>{{totalMoney }}</p>
    </div>
</template>

<script>
	export default {
	    name: 'app-heder',
	    data(){
		  return{
			   items:[
				   {
				   	id:1,
				   	"name":"蛋糕",
				   	"nume":1,
				   	"price":20
				   },
				   {
				   	id:2,
				   	"name":"西瓜",
				   	"nume":1,
				   	"price":20
				   },
				   {
				   	id:3,
				   	"name":"草莓",
				   	"nume":1,
				   	"price":20
				   },
				   {
				   	id:4,
				   	"name":"樱桃",
				   	"nume":1,
				   	"price":20
				   }
			   ],  
			   itemsd:[],
			   isok:false  ,//判断CheckBox是否选中
			   // zongprice:0
		    }
	           
	    },
	         methods:{
				 subt(k){
				 	this.items[k].nume--
				 	if(this.items[k].nume==0){
				 		this.items.splice(k,1);
				 	}
				 },
				 adds(k){
				 	this.items[k].nume++
				 },
				 delect(k){
				 	this.items.splice(k,1);
				 },
	             demo(){   //全选/反选
	                 if( !this.isok){ //全选中 所有CheckBox
	                     this.items.forEach( (fruit) =>{
	                         this.itemsd.push(fruit);
							 // this.zongprice+=fruit.nume*fruit.price;
	                     }, this);
	                 }else{   //反选清楚所有CheckBox选中
	                   this.itemsd=[]
					    // this.zongprice=0;
	                 }
	             },
	             demo2(){
// 					 this.shoppings.forEach((item,k)=>{
// 					 	if(item.checked){
// 					 		total+=item.nume*item.price;
// 					 	}
// 					 })
	                 if(this.items.length==this.itemsd.length){ //判断每一个CheckBox是否选中   全选中让全选反选按钮选中
	                     this.isok=true;
						 
	                 }else{  // 不选中 让全选反选按钮不选中
	                     this.isok=false;
	                 }
	             },
				 
	         },
			 computed:{
				 //算价格
			     totalMoney:function(item,index){
			         let sum = 0;
			         for(let i=0;i<this.itemsd.length;i++){
			             sum += this.itemsd[i].nume*this.itemsd[i].price;
			         };
			         return sum;
			     },
				 }
			 
	
	}
	 
</script>

<style>
</style>



