<template>
	<el-container class="get-blog" v-themd="'wide'">
		<el-card class="box-card">
		  <div>
			<el-input v-model="serchInput" placeholder="请输入内容">
			</el-input>
		  </div>
			<ul>
				<li v-for="blog in fiterBlog"  :key="blog.id"><router-link v-bind:to="'/blogDetail/'+blog.id">
					<h2  v-demo>{{blog.title | toupperCase}}</h2>
					<div v-demo>{{blog.content | slice}}</div>
					</router-link>
				</li>
			</ul>
		</el-card>
	</el-container>
	
</template>

<script>
	export default{
		name:"get-blog",
		data(){
			return{
				blogs:[],
				serchInput:"",
				// length:0,
				currentPage1: 1,
				
			}
		},
		methods:{
			getBlog(){
				this.$http.get("/api/users")
				.then((res)=>{
					// this.blogs=res.data.slice(0,4);//显示三条数据
					// this.length=res.data.length;
					this.blogs=res.data;
					
				})
				.catch((error)=>{
					console.log(error)
				})
			},
			
// 			  handleSizeChange(val) {
// 				console.log(`每页 ${val} 条`);
// 			  },
// 			  handleCurrentChange(val) {
// 				console.log(`当前页: ${val}`);
// 			  }
	    },
		created(){
			this.getBlog();
		},
		activated(){
         this.getBlog();
        },
		computed:{
			fiterBlog(){
				return this.blogs.filter((blog)=>{
					return blog.title.match(this.serchInput) || blog.content.match(this.serchInput)
				})
			}
		},
		//自定义过滤器
		filters:{
			toupperCase:function(value){
				
				return value.toUpperCase()
			},
			slice(value){
				if(value.length>5){
					return value.slice(0,5)+'...'
				}else{
					return value
				}
				
			}
		},
		//自定义指令
		directives:{
			demo:{
				bind(el, binding, vnode){
				 el.style.color="#"+Math.random().toString(16).slice(2,8);
			  }
			},
			themd:{
				bind(el,binding,vnode){
					if(binding.value=='wide'){
						el.style.maxWidth="1000px";
						
					}else if(binding.value=="narrw"){
						el.style.background="red"
					}
				}
			}
		}
	}
</script>

<style scoped="scoped">
	.el-container{flex-direction:column}
	.get-blog{margin: 0 auto;}
	.box-card{width:500px;margin:0 auto}
	ul{list-style: none;}
	ul li{background: #fff;border: 1px solid #ddd;border-radius: 5px;margin-bottom: 20px;width:100%;}
	.block{width:500;margin: 0 auto;}
	a{text-decoration:none;}
</style>



