<template>
	<el-container class="get-blog" >
		<el-card class="box-card">
			<h1>详情</h1>
				<h2>博客标题:{{blog.title}}</h2>
				<div>博客内容:{{blog.content}}</div>
				<div>博客分类:
				<ul>
					<li v-for="category in blog.categories" :key="category">{{category}}</li>
				</ul>
				</div>
				<div>作者:{{blog.auth}}</div>
				<button @click="delBlog()">删除</button>
				<router-link v-bind:to="'/edit/'+blog.id">编辑</router-link>
		</el-card>
	</el-container>
</template>

<script>
	export default{
		name:"get-blog",
		data(){
			return{
				id:this.$route.params.id,
				blog:[]
			}
		},
		methods:{
			fetchCustomers(id){
				this.$http.get("/api/users/"+this.id)
				.then((response)=>{
					this.blog=response.data;
				})
			},
			delBlog(){
				this.$http.delete("/api/users/"+this.id)
				.then((res)=>{
					this.$router.push({path:'/'})
					
				})
			}
	    },
		 created(){
			  this.fetchCustomers(this.$route.params.id);
		}
	
	}
</script>

<style scoped="scoped">
	.el-container{flex-direction:column}
	.get-blog{margin: 0 auto;}
	.box-card{width:500px;margin:0 auto}
	ul{list-style: none;}
	ul li{background: #fff;border: 1px solid #ddd;border-radius: 5px;margin-bottom: 20px;width:100%;}
	.block{width:500;margin: 0 auto;}
</style>



