<template>
	<div class="label">
		<el-tabs v-model="activeName">
			<el-tab-pane label="用户管理" name="first">用户管理</el-tab-pane>
			<el-tab-pane label="配置管理" name="second">配置管理</el-tab-pane>
			<el-tab-pane label="角色管理" name="third">角色管理</el-tab-pane>
			<el-tab-pane label="定时任务补偿" name="fourth">定时任务补偿</el-tab-pane>
		</el-tabs>
		
		<el-table :data="list.slice((currpage - 1) * pagesize, currpage * pagesize)"  border style="width: 100%">
			
	    	<el-table-column type="index" label="序号" >
	        </el-table-column>
	        <el-table-column prop="title" label="标题">
	        </el-table-column>
	        <el-table-column prop="content" label="内容">
	        </el-table-column>
	        <el-table-column prop="auth" label="作者">
	        </el-table-column>
	    </el-table>
			<el-pagination background 
				layout="prev, pager, next, sizes, total, jumper"
				:page-sizes="[5, 10, 15, 20]" 
				:page-size="pagesize" 
				:total="list.length"
				
				@current-change="handleCurrentChange" 
				@size-change="handleSizeChange" 
				>
			</el-pagination>
		<!-- <el-pagination background 
			layout="prev, pager, next, sizes, total, jumper"
			:page-sizes="[5, 10, 15, 20]" //每页展示条选择组件
			:page-size="pagesize" //每页展示条
			:total="list.length"
			@current-change="handleCurrentChange"  // currentPage改变时会触发
			@size-change="handleSizeChange"  //pagesize改变时触发
			>
		</el-pagination> -->
	</div>
</template>

<script>
	export default {
		data(){
			return{
				msg: 8888,
				list:[],
				pagesize: 5,
				currpage: 1,
				activeName: 'first',
				tabPosition: 'left',
				 editableTabsValue: '1',
				tabIndex: 1,
			}
				
		},
		methods: {
			getlist() {
				let starttime = new Date();
				this.$http.get('/api/users/')
				.then(res => {
					console.log(res)
					// console.log(new Date() - starttime)
					this.list = res.data;
				}).catch(err => {
					console.error(err)
					this.$alert('请求超时，刷新重试！')
				})
			},
			handleClick(tab, event) {
			         console.log(tab, event);
			},
			handleCurrentChange(cpage) {
				this.currpage = cpage;
			},
			handleSizeChange(psize) {
				this.pagesize = psize;
			}
		},
		mounted() {
			this.getlist()
		}

  };
</script>

<style scoped="scoped">
	.label{width: 500px;display: flex;margin: 0 auto;flex-direction: column;}
</style>



